import historyImgSm from '../../assets/img/history_bg_sm.png'
import historyImgLg from '../../assets/img/history_bg_lg.png'
import styles from "./history.module.scss";

const History = () => {
  return (
    <div className="container">
      <div className="content">
        <div className={styles.title}>
          <h2>Наша История</h2>
        </div>
        <div className="text">
          <p>
            Как и у любого другого самобытного места, у нас есть своя, особая
            история. Идея ресторана пришла основателям неожиданно. Во время
            прогулки по лесу создатель нашего ресторана застрял в сотнях
            километров от ближайшего населенного пункта. Вдали от цивилизации и
            связи им пришлось навремя обустровать себе нехитрый быт, добывать и
            готовить себе еду.
          </p>
        </div>
        <div className="statistic">
          <div className="item">
            <h3>93</h3>
            <h4>Напитки</h4>
          </div>
          <div className="item">
            <h3>Напитки</h3>
            <h4>Еда</h4>
          </div>
          <div className="item">
            <h3>71</h3>
            <h4>Закуски</h4>
          </div>
        </div>
      </div>
      <div className={styles.images}>
        <picture>
          <source srcSet={historyImgSm} media="(max-width: 320px)"></source>
          <source srcSet={historyImgLg} media="(max-widht: 1440px)"></source>
          <img src={historyImgLg} alt="images"/>
        </picture>
      </div>
    </div>
  );
};

export default History;
