import sprite from "../../assets/sprites/sprite.svg";

import styles from "./cards.module.scss";
import "../../theme/global.scss";

const title0 = (
  <h2>
    Магическая <span>Атмосфера</span>
  </h2>
);
const text0 = (
  <h3>
    В нашем заведении царит магическая атмосфера наполненная вкусными ароматами
  </h3>
);

const title1 = (
  <h2>
    Лучшее качество <span>Еды</span>
  </h2>
);
const text1 = <h3>Качество нашей Еды - отменное!</h3>;

const title2 = (
  <h2>
    Недорогая <span>Еда</span>
  </h2>
);
const text2 = (
  <h3>
    Стоимость нашей Еды зависит только от ее количества. Качество всегда на
    высоте!
  </h3>
);

const cards = [
  {
    id: 0,
    title: title0,
    text: text0,
    stack: "stack-sm"
  },
  {
    id: 1,
    title: title1,
    text: text1,
    stack: "stack-md"
  },
  {
    id: 2,
    title: title2,
    text: text2,
    stack: "stack-sm"
  },
];

const Cards = () => {
  const listCards = cards.map((card) => {
    const { id, title, text, stack } = card;

    return (
      <div className={styles.card} key={id}>
        <div className={styles[stack]}>
          <div className={styles.img}>
            <svg className={styles.icon}>
              <use href={sprite + "#menu"}></use>
            </svg>
          </div>
          {title}
          {text}
        </div>
      </div>
    );
  });
  return (
    <div className="container">
      <div className={styles.cards}>{listCards}</div>
    </div>
  );
};

export default Cards;
